// 商城-增票资质相关接口 qualifications
import axios from './../fetch'

// 增票资质列表分页
export const qualifiedList = params => { return axios.get('/admin/platform/memberinvoicequalified/page', { params: params }) }

// 增票资质列表详情
export const qualifiedGet = params => { return axios.get('/admin/platform/memberinvoicequalified/get', { params: params }) }

// 增票资质列表修改
export const qualifiedUpdate = params => { return axios.post('/admin/platform/memberinvoicequalified/update', params) }

// 增票资质列表新增
export const qualifiedAdd = params => { return axios.post('/admin/platform/memberinvoicequalified/save', params) }

// 增票资质列表删除
export const qualifiedDelete = params => { return axios.post('/admin/platform/memberinvoicequalified/delete', params) }

// 增票资质是否注册
export const qualifiedIsrequired = params => { return axios.get('/admin/platform/memberinvoicequalified/checkMemberRequired', { params: params }) }




