<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>显示所有会员需要开具增值税的资质，仅限每个用户一项。</li>
      <li>可以对增票资质进行修改操作</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>会员账号</label>
        <el-input v-model="queryParams.username" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>邮箱</label>
        <el-input v-model="queryParams.email" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>手机</label>
        <el-input v-model="queryParams.phone" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="member.username" label="会员账号" width="110"></el-table-column>
      <el-table-column prop="companyName" label="公司名称" min-width="240"></el-table-column>
      <el-table-column prop="address" label="注册地址" min-width="240"></el-table-column>
      <el-table-column prop="phoneNumber" label="注册电话" min-width="180"></el-table-column>
      <el-table-column prop="status" label="状态" min-width="160">
        <div slot-scope="scope">
          <span class="success">正常使用</span>
        </div>
      </el-table-column>
      <el-table-column prop="detail" label="备注" min-width="160">
        <template slot-scope="scope">
         <div class="text-overflow" :title="scope.row.detail"> {{scope.row.detail}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作选项" width='90' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info' 
            v-prem="'/qualifications/edit'"    
            @click="$routerLink('/qualifications/edit','update',{id:scope.row.id})">修改</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>

<script>
import { qualifiedList } from '@/api/users/qualifications.js'
import list from '@/mixins/list'
var defaultData = {
  roleName: '',
  all: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {

  },
  created() {
    this.getTableData(qualifiedList, this.queryParams)
  }
}
</script>

<style>
</style>



